"use client";

import React, { useState, useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { Progress } from '@/components/ui/progress';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import BinInfoCard from './BinInfoCard';

interface BulkResult {
  bin: string;
  data?: any;
  error?: string;
  isLuhnValid?: boolean;
}

interface BulkAnalytics {
  totalRequested: number;
  successful: number;
  failed: number;
  processingTimeMs: number;
  averageTimePerBin: number;
  luhnValidCount: number;
  luhnInvalidCount: number;
  cardTypes: Record<string, number>;
  countries: Record<string, number>;
  schemes: Record<string, number>;
}

export default function BulkBinCheck() {
  const [inputText, setInputText] = useState('');
  const [results, setResults] = useState<BulkResult[]>([]);
  const [analytics, setAnalytics] = useState<BulkAnalytics | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [progress, setProgress] = useState(0);
  const [countryFilter, setCountryFilter] = useState<string>('all');

  const handleBulkCheck = async () => {
    if (!inputText.trim()) {
      setError('Please enter BIN numbers to check');
      return;
    }

    setLoading(true);
    setError('');
    setResults([]);
    setAnalytics(null);
    setProgress(0);

    try {
      // Parse input - support both comma-separated and newline-separated
      const bins = inputText
        .split(/[,\n]/)
        .map(bin => bin.trim())
        .filter(bin => bin.length > 0);

      if (bins.length === 0) {
        setError('No valid BIN numbers found');
        setLoading(false);
        return;
      }

      if (bins.length > 1000) {
        setError('Maximum 1000 BINs allowed per bulk request');
        setLoading(false);
        return;
      }

      // Simulate progress updates
      const progressInterval = setInterval(() => {
        setProgress(prev => Math.min(prev + 10, 90));
      }, 200);

      const response = await fetch('/api/bulkBinLookup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ bins }),
      });

      clearInterval(progressInterval);
      setProgress(100);

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Bulk lookup failed');
      }

      const data = await response.json();
      setResults(data.results);
      setAnalytics(data.analytics);

    } catch (err: any) {
      setError(err.message || 'Failed to perform bulk lookup');
    } finally {
      setLoading(false);
      setTimeout(() => setProgress(0), 1000);
    }
  };

  const handleClear = () => {
    setInputText('');
    setResults([]);
    setAnalytics(null);
    setError('');
    setCountryFilter('all');
  };

  const handleExportCsv = async () => {
    if (results.length === 0) return;

    try {
      const response = await fetch('/api/bulkBinLookup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ 
          bins: results.map(r => r.bin),
          format: 'csv'
        }),
      });

      if (response.ok) {
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = 'bin-lookup-results.csv';
        document.body.appendChild(a);
        a.click();
        window.URL.revokeObjectURL(url);
        document.body.removeChild(a);
      }
    } catch (err) {
      console.error('Export failed:', err);
    }
  };

  const handleExportValidBins = () => {
    const validResults = results.filter(r => !r.error);
    if (validResults.length === 0) return;

    const csvContent = [
      ['BIN', 'Bank Name', 'Card Type', 'Scheme', 'Country', 'Currency'].join(','),
      ...validResults.map(result => [
        result.bin,
        result.data?.bank?.name || 'N/A',
        result.data?.type || 'N/A',
        result.data?.scheme || 'N/A',
        result.data?.country?.name || 'N/A',
        result.data?.country?.currency || 'N/A'
      ].map(cell => `"${cell}"`).join(','))
    ].join('\n');

    const blob = new Blob([csvContent], { type: 'text/csv' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'valid-bins-only.csv';
    document.body.appendChild(a);
    a.click();
    window.URL.revokeObjectURL(url);
    document.body.removeChild(a);
  };

  // Get unique countries for filter
  const availableCountries = useMemo(() => {
    const countries = new Set<string>();
    results.forEach(result => {
      if (result.data?.country?.name && !result.error) {
        countries.add(result.data.country.name);
      }
    });
    return Array.from(countries).sort();
  }, [results]);

  // Filter results by country
  const filteredResults = useMemo(() => {
    if (countryFilter === 'all') return results;
    return results.filter(result => 
      result.data?.country?.name === countryFilter
    );
  }, [results, countryFilter]);

  const successfulResults = filteredResults.filter(r => !r.error);
  const failedResults = filteredResults.filter(r => r.error);

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center justify-between">
            <span>Bulk BIN Checker</span>
            <Badge variant="outline">Max 1000 BINs</Badge>
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <label className="text-sm font-medium mb-2 block">
              Enter BIN numbers (comma or newline separated):
            </label>
            <Textarea
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              placeholder="411111,555555,378282&#10;or&#10;411111&#10;555555&#10;378282&#10;or full card numbers like 4111111111111111"
              rows={6}
              className="font-mono"
            />
            <p className="text-xs text-muted-foreground mt-1">
              Example: 411111,555555,378282 or one BIN per line or full card numbers (BIN will be extracted automatically)
            </p>
          </div>

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {loading && (
            <div className="space-y-2">
              <div className="flex items-center justify-between text-sm">
                <span>Processing BINs...</span>
                <span>{progress}%</span>
              </div>
              <Progress value={progress} className="w-full" />
            </div>
          )}

          <div className="flex gap-2 flex-wrap">
            <Button 
              onClick={handleBulkCheck} 
              disabled={loading || !inputText.trim()}
              className="flex-1"
            >
              {loading ? 'Processing...' : 'Check BINs'}
            </Button>
            <Button 
              variant="outline" 
              onClick={handleClear}
              disabled={loading}
            >
              Clear
            </Button>
            {results.length > 0 && (
              <>
                <Button 
                  variant="secondary" 
                  onClick={handleExportCsv}
                  disabled={loading}
                >
                  Export All CSV
                </Button>
                <Button 
                  variant="default" 
                  onClick={handleExportValidBins}
                  disabled={loading || successfulResults.length === 0}
                >
                  Export Valid BINs
                </Button>
              </>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Country Filter */}
      {availableCountries.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Filter Results</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center gap-4">
              <label className="text-sm font-medium">Filter by Country:</label>
              <Select value={countryFilter} onValueChange={setCountryFilter}>
                <SelectTrigger className="w-48">
                  <SelectValue placeholder="Select country" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Countries ({results.filter(r => !r.error).length})</SelectItem>
                  {availableCountries.map(country => {
                    const count = results.filter(r => r.data?.country?.name === country && !r.error).length;
                    return (
                      <SelectItem key={country} value={country}>
                        {country} ({count})
                      </SelectItem>
                    );
                  })}
                </SelectContent>
              </Select>
              {countryFilter !== 'all' && (
                <Badge variant="secondary">
                  Showing {filteredResults.filter(r => !r.error).length} results
                </Badge>
              )}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Analytics Dashboard */}
      {analytics && (
        <Card>
          <CardHeader>
            <CardTitle>Bulk Analysis Summary</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
              <div className="text-center p-3 bg-muted rounded-lg">
                <p className="text-2xl font-bold text-green-600">{analytics.successful}</p>
                <p className="text-sm text-muted-foreground">Successful (Luhn Valid)</p>
              </div>
              <div className="text-center p-3 bg-muted rounded-lg">
                <p className="text-2xl font-bold text-red-600">{analytics.failed}</p>
                <p className="text-sm text-muted-foreground">Failed (Luhn Invalid)</p>
              </div>
              <div className="text-center p-3 bg-muted rounded-lg">
                <p className="text-2xl font-bold text-blue-600">{analytics.processingTimeMs}ms</p>
                <p className="text-sm text-muted-foreground">Total Time</p>
              </div>
              <div className="text-center p-3 bg-muted rounded-lg">
                <p className="text-2xl font-bold text-purple-600">{analytics.averageTimePerBin}ms</p>
                <p className="text-sm text-muted-foreground">Avg per BIN</p>
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {/* Card Types */}
              <div>
                <h4 className="font-semibold mb-2">Card Types</h4>
                <div className="space-y-2">
                  {Object.entries(analytics.cardTypes).map(([type, count]) => (
                    <div key={type} className="flex justify-between items-center">
                      <Badge variant="outline" className="capitalize">{type}</Badge>
                      <span className="text-sm font-medium">{count}</span>
                    </div>
                  ))}
                </div>
              </div>

              {/* Countries */}
              <div>
                <h4 className="font-semibold mb-2">Countries</h4>
                <div className="space-y-2">
                  {Object.entries(analytics.countries).slice(0, 5).map(([country, count]) => (
                    <div key={country} className="flex justify-between items-center">
                      <span className="text-sm truncate">{country}</span>
                      <span className="text-sm font-medium">{count}</span>
                    </div>
                  ))}
                </div>
              </div>

              {/* Schemes */}
              <div>
                <h4 className="font-semibold mb-2">Card Schemes</h4>
                <div className="space-y-2">
                  {Object.entries(analytics.schemes).map(([scheme, count]) => (
                    <div key={scheme} className="flex justify-between items-center">
                      <Badge variant="secondary" className="capitalize">{scheme}</Badge>
                      <span className="text-sm font-medium">{count}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Results */}
      {results.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>
              Results ({filteredResults.length} BINs {countryFilter !== 'all' ? `from ${countryFilter}` : 'processed'})
            </CardTitle>
          </CardHeader>
          <CardContent>
            <Tabs defaultValue="successful" className="w-full">
              <TabsList className="grid w-full grid-cols-3">
                <TabsTrigger value="successful">
                  Valid (Luhn Passed) ({successfulResults.length})
                </TabsTrigger>
                <TabsTrigger value="failed">
                  Invalid (Luhn Failed) ({failedResults.length})
                </TabsTrigger>
                <TabsTrigger value="all">
                  All Results
                </TabsTrigger>
              </TabsList>

              <TabsContent value="successful" className="space-y-4">
                {successfulResults.length === 0 ? (
                  <p className="text-center text-muted-foreground py-8">
                    No BINs passed Luhn validation {countryFilter !== 'all' ? `from ${countryFilter}` : ''}
                  </p>
                ) : (
                  successfulResults.map((result, index) => (
                    <BinInfoCard
                      key={`success-${index}`}
                      bin={result.bin}
                      data={result.data}
                      isLuhnValid={result.isLuhnValid || false}
                    />
                  ))
                )}
              </TabsContent>

              <TabsContent value="failed" className="space-y-4">
                {failedResults.length === 0 ? (
                  <p className="text-center text-muted-foreground py-8">
                    No failed results {countryFilter !== 'all' ? `from ${countryFilter}` : ''}
                  </p>
                ) : (
                  failedResults.map((result, index) => (
                    <Card key={`failed-${index}`}>
                      <CardContent className="pt-6">
                        <div className="flex items-center justify-between">
                          <div>
                            <p className="font-medium">BIN: {result.bin}</p>
                            <p className="text-sm text-red-600">{result.error}</p>
                          </div>
                          <Badge variant="destructive">Failed Luhn Check</Badge>
                        </div>
                      </CardContent>
                    </Card>
                  ))
                )}
              </TabsContent>

              <TabsContent value="all" className="space-y-4">
                {filteredResults.map((result, index) => (
                  <div key={`all-${index}`}>
                    {result.error ? (
                      <Card>
                        <CardContent className="pt-6">
                          <div className="flex items-center justify-between">
                            <div>
                              <p className="font-medium">BIN: {result.bin}</p>
                              <p className="text-sm text-red-600">{result.error}</p>
                            </div>
                            <Badge variant="destructive">Failed Luhn Check</Badge>
                          </div>
                        </CardContent>
                      </Card>
                    ) : (
                      <BinInfoCard
                        bin={result.bin}
                        data={result.data}
                        isLuhnValid={result.isLuhnValid || false}
                      />
                    )}
                  </div>
                ))}
              </TabsContent>
            </Tabs>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
