"use client";

import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { Progress } from '@/components/ui/progress';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';

interface Country {
  code: string;
  name: string;
  emoji: string;
  currency: string;
}

interface GeneratedResult {
  bins: string[];
  country: string;
  countryCode: string;
  currency: string;
  emoji: string;
  count: number;
  requestedCount: number;
  processingTimeMs: number;
  options: {
    scheme: string;
    type: string;
    length: number;
  };
}

export default function BinGenerator() {
  const [countries, setCountries] = useState<Country[]>([]);
  const [schemes, setSchemes] = useState<string[]>([]);
  const [types, setTypes] = useState<string[]>([]);
  
  const [selectedCountry, setSelectedCountry] = useState<string>('');
  const [selectedScheme, setSelectedScheme] = useState<string>('');
  const [selectedType, setSelectedType] = useState<string>('');
  const [count, setCount] = useState<number>(1);
  const [length, setLength] = useState<number>(8);
  
  const [result, setResult] = useState<GeneratedResult | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [progress, setProgress] = useState(0);

  // Load countries on component mount
  useEffect(() => {
    loadCountries();
  }, []);

  // Load schemes when country changes
  useEffect(() => {
    if (selectedCountry) {
      loadSchemes(selectedCountry);
    } else {
      setSchemes([]);
      setTypes([]);
    }
  }, [selectedCountry]);

  // Load types when country or scheme changes
  useEffect(() => {
    if (selectedCountry) {
      loadTypes(selectedCountry, selectedScheme);
    } else {
      setTypes([]);
    }
  }, [selectedCountry, selectedScheme]);

  const loadCountries = async () => {
    try {
      const response = await fetch('/api/generateBins?action=countries');
      const data = await response.json();
      setCountries(data.countries || []);
    } catch (err) {
      console.error('Failed to load countries:', err);
    }
  };

  const loadSchemes = async (countryCode: string) => {
    try {
      const response = await fetch(`/api/generateBins?action=schemes&countryCode=${countryCode}`);
      const data = await response.json();
      setSchemes(data.schemes || []);
      setSelectedScheme(''); // Reset scheme selection
    } catch (err) {
      console.error('Failed to load schemes:', err);
    }
  };

  const loadTypes = async (countryCode: string, scheme?: string) => {
    try {
      const url = `/api/generateBins?action=types&countryCode=${countryCode}${scheme ? `&scheme=${scheme}` : ''}`;
      const response = await fetch(url);
      const data = await response.json();
      setTypes(data.types || []);
      setSelectedType(''); // Reset type selection
    } catch (err) {
      console.error('Failed to load types:', err);
    }
  };

  const handleGenerate = async () => {
    if (!selectedCountry) {
      setError('Please select a country');
      return;
    }

    if (count < 1 || count > 500) {
      setError('Count must be between 1 and 500');
      return;
    }

    setLoading(true);
    setError('');
    setResult(null);
    setProgress(0);

    try {
      // Simulate progress
      const progressInterval = setInterval(() => {
        setProgress(prev => Math.min(prev + 20, 90));
      }, 100);

      const response = await fetch('/api/generateBins', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          countryCode: selectedCountry,
          count,
          scheme: selectedScheme || undefined,
          type: selectedType || undefined,
          length
        }),
      });

      clearInterval(progressInterval);
      setProgress(100);

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Generation failed');
      }

      const data = await response.json();
      setResult(data);

    } catch (err: any) {
      setError(err.message || 'Failed to generate BINs');
    } finally {
      setLoading(false);
      setTimeout(() => setProgress(0), 1000);
    }
  };

  const handleClear = () => {
    setSelectedCountry('');
    setSelectedScheme('');
    setSelectedType('');
    setCount(1);
    setLength(8);
    setResult(null);
    setError('');
  };

  const handleDownload = async (format: 'txt' | 'csv') => {
    if (!result || !selectedCountry) return;

    try {
      const response = await fetch('/api/generateBins', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          countryCode: selectedCountry,
          count,
          scheme: selectedScheme || undefined,
          type: selectedType || undefined,
          length,
          format
        }),
      });

      if (response.ok) {
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = `generated-bins-${selectedCountry}-${count}.${format}`;
        document.body.appendChild(a);
        a.click();
        window.URL.revokeObjectURL(url);
        document.body.removeChild(a);
      }
    } catch (err) {
      console.error('Download failed:', err);
    }
  };

  const selectedCountryData = countries.find(c => c.code === selectedCountry);

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center justify-between">
            <span>BIN Generator</span>
            <Badge variant="outline">Max 500 BINs</Badge>
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Country Selection */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="text-sm font-medium mb-2 block">
                Select Country *
              </label>
              <Select value={selectedCountry} onValueChange={setSelectedCountry}>
                <SelectTrigger>
                  <SelectValue placeholder="Choose a country" />
                </SelectTrigger>
                <SelectContent>
                  {countries.map(country => (
                    <SelectItem key={country.code} value={country.code}>
                      {country.emoji} {country.name} ({country.currency})
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div>
              <label className="text-sm font-medium mb-2 block">
                Number of BINs *
              </label>
              <Input
                type="number"
                min="1"
                max="500"
                value={count}
                onChange={(e) => setCount(parseInt(e.target.value) || 1)}
                placeholder="Enter count (1-500)"
              />
            </div>
          </div>

          {/* Optional Filters */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div>
              <label className="text-sm font-medium mb-2 block">
                Card Scheme (Optional)
              </label>
              <Select value={selectedScheme} onValueChange={setSelectedScheme}>
                <SelectTrigger>
                  <SelectValue placeholder="Any scheme" />
                </SelectTrigger>
              <SelectContent>
                <SelectItem value="any">Any Scheme</SelectItem>
                {schemes.map(scheme => (
                  <SelectItem key={scheme} value={scheme}>
                    {scheme.charAt(0).toUpperCase() + scheme.slice(1)}
                  </SelectItem>
                ))}
              </SelectContent>
              </Select>
            </div>

            <div>
              <label className="text-sm font-medium mb-2 block">
                Card Type (Optional)
              </label>
              <Select value={selectedType} onValueChange={setSelectedType}>
                <SelectTrigger>
                  <SelectValue placeholder="Any type" />
                </SelectTrigger>
              <SelectContent>
                <SelectItem value="any">Any Type</SelectItem>
                {types.map(type => (
                  <SelectItem key={type} value={type}>
                    {type.charAt(0).toUpperCase() + type.slice(1)}
                  </SelectItem>
                ))}
              </SelectContent>
              </Select>
            </div>

            <div>
              <label className="text-sm font-medium mb-2 block">
                BIN Length
              </label>
              <Select value={length.toString()} onValueChange={(value) => setLength(parseInt(value))}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="6">6 digits</SelectItem>
                  <SelectItem value="8">8 digits</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Selected Country Info */}
          {selectedCountryData && (
            <div className="p-4 bg-muted rounded-lg">
              <div className="flex items-center gap-2 mb-2">
                <span className="text-2xl">{selectedCountryData.emoji}</span>
                <div>
                  <p className="font-medium">{selectedCountryData.name}</p>
                  <p className="text-sm text-muted-foreground">
                    Currency: {selectedCountryData.currency} ‚Ä¢ Available schemes: {schemes.length}
                  </p>
                </div>
              </div>
            </div>
          )}

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {loading && (
            <div className="space-y-2">
              <div className="flex items-center justify-between text-sm">
                <span>Generating BINs...</span>
                <span>{progress}%</span>
              </div>
              <Progress value={progress} className="w-full" />
            </div>
          )}

          <div className="flex gap-2">
            <Button 
              onClick={handleGenerate} 
              disabled={loading || !selectedCountry}
              className="flex-1"
            >
              {loading ? 'Generating...' : 'Generate BINs'}
            </Button>
            <Button 
              variant="outline" 
              onClick={handleClear}
              disabled={loading}
            >
              Clear
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Results */}
      {result && (
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center justify-between">
              <span>Generated BINs</span>
              <div className="flex gap-2">
                <Button 
                  variant="outline" 
                  size="sm"
                  onClick={() => handleDownload('txt')}
                >
                  Download TXT
                </Button>
                <Button 
                  variant="outline" 
                  size="sm"
                  onClick={() => handleDownload('csv')}
                >
                  Download CSV
                </Button>
              </div>
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            {/* Summary */}
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              <div className="text-center p-3 bg-muted rounded-lg">
                <p className="text-2xl font-bold text-green-600">{result.count}</p>
                <p className="text-sm text-muted-foreground">Generated</p>
              </div>
              <div className="text-center p-3 bg-muted rounded-lg">
                <p className="text-2xl font-bold text-blue-600">{result.processingTimeMs}ms</p>
                <p className="text-sm text-muted-foreground">Processing Time</p>
              </div>
              <div className="text-center p-3 bg-muted rounded-lg">
                <p className="text-2xl font-bold text-purple-600">{result.options.length}</p>
                <p className="text-sm text-muted-foreground">Digits Length</p>
              </div>
              <div className="text-center p-3 bg-muted rounded-lg">
                <p className="text-2xl font-bold text-orange-600">100%</p>
                <p className="text-sm text-muted-foreground">Luhn Valid</p>
              </div>
            </div>

            <Separator />

            {/* Country and Options Info */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="p-4 bg-muted rounded-lg">
                <h4 className="font-semibold mb-2 flex items-center gap-2">
                  {result.emoji} Country Information
                </h4>
                <div className="space-y-1 text-sm">
                  <p><strong>Country:</strong> {result.country}</p>
                  <p><strong>Currency:</strong> {result.currency}</p>
                  <p><strong>Code:</strong> {result.countryCode}</p>
                </div>
              </div>
              <div className="p-4 bg-muted rounded-lg">
                <h4 className="font-semibold mb-2">Generation Options</h4>
                <div className="space-y-1 text-sm">
                  <p><strong>Scheme:</strong> {result.options.scheme}</p>
                  <p><strong>Type:</strong> {result.options.type}</p>
                  <p><strong>Length:</strong> {result.options.length} digits</p>
                </div>
              </div>
            </div>

            <Separator />

            {/* Generated BINs */}
            <div>
              <h4 className="font-semibold mb-3">Generated BINs ({result.count})</h4>
              <Textarea
                value={result.bins.join('\n')}
                readOnly
                rows={Math.min(result.bins.length, 10)}
                className="font-mono text-sm"
                placeholder="Generated BINs will appear here..."
              />
              <p className="text-xs text-muted-foreground mt-2">
                All generated BINs are Luhn algorithm compliant and ready for testing.
              </p>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
